
.main-nav {min-height: 50px;
width: 100vw;
margin: 0% auto;
border-top: solid 3px #ef8236; 
box-shadow: 0px 1px 5px #00800833; 
position: fixed;
z-index: 5;
top: 0%;
left: 84;
background-color: #f8f9f9;
display: flex;
justify-content: center;
align-items: center;

}



.navbar{
    height: 100%;
    min-width: 85%;
    display: flex;
    align-items: center;
    justify-content:space-between;
}

.nav-logo{
    padding:5px 25px;
}

.nav-items{
    margin:0px 3px;
    font-size:small;
    font-weight:500;
    text-decoration:none;
    color:rgb(69,69,69);
    transition:0.2s;

}

.nav-btn{
    cursor:pointer;
    border-radius: 20px;
    padding:10px 20px;
    text-decoration: none;
    color:rgb(38, 31, 31)
}

.nav-item:hover{
    background-color: rgb(226,226,226);
}

.navbar form{
    flex-grow:1;
    padding:0px 12px;
    position:relative;
}

.navbar form input{
    min-width:90%;
    margin:0;
    padding:8px 10px 8px 32px;
    font-size:13px;
    border:solid 1px #0000003e;
    border-radius:3px;
}

.navbar form .search-icon{
    /* position:absolute;
    left:25px;
    top:8px; */
}

.navbar form{
    display:flex;
}


.nav-links{
    padding:7px 13px;
    border:solid 1px blue;
    border-radius:3px;
    background-color: #e7f8fe;
    cursor:pointer;
    text-decoration: none;
    color:rgb(45, 41, 41);
    
}

.nav-links:hover{
    background-color: #d3e4eb;
}

.change{
    display: flex;
    justify-content: space-between;
}